@import url(https://fonts.googleapis.com/css?family=Source+Serif+Pro&display=swap);
@import url(https://fonts.googleapis.com/css?family=Raleway&display=swap);
.header {
  max-width: 50rem;
  margin: 2rem 5rem;
}

.container {
  max-width: 50rem;
  margin: 2rem 5rem 12rem 5rem;
}

.index-title {
  text-decoration: none;
  font-size: 15pt;
  color: #202340;
}

.index-content {
  margin-top: 0.75em;
  color: #38383d;
}

p {
  margin-top: 1.45em;
}

.title,
.date,
.author {
  font-family: 'Raleway', sans-serif;
}

h1 {
  font-size: 32pt;
  margin-top: 1em;
  margin-bottom: 0;
  text-decoration: none;
  color: #202340;
}

.author, .date {
  font-size: 13pt;
  color: #38383d;
}

.content {
  margin-bottom: 3em;
  margin-top: 4em;
}

.content * {
  font-family: 'Source Serif Pro', serif;
  font-size: 14pt;
  line-height: 1.45em;
}

.content h2 {
  padding-left: 2%;
  margin-top: 2.5em;
  border-bottom-style: solid;
  border-bottom-color: #b1b1b3;
  border-bottom-width: 1px;
  font-family: 'Raleway', sans-serif;
  color: #c44e27;
  font-size: 28pt;
  font-weight: 100;
  margin-bottom: 0;
}

.content blockquote {
  font-size: 12.5pt;
}

h2.subhed + p {
  margin-top: 0.625em;
}

* {
  font-family: monospace;
  font-size: 11pt;
  color: #202340;
}

.header-span {
  margin-right: 5rem;
}

.header-link {
  text-decoration: none;
  color: black;
}

@media only screen and (max-width: 57rem) {
  .container {
    max-width: 50rem;
    margin: 2rem 2rem 5rem 2rem;
  }

  .header {
    max-width: 50rem;
    margin: 2rem;
  }
}

@media only screen and (max-width: 34rem) {
  .container {
    max-width: 31rem;
    margin: 1.5rem 1.5rem 5rem 1.5rem;
  }

  .header {
    max-width: 31rem;
    margin: 1.5rem;
  }
}

